address, article, body, div, figure, footer, form, h2, h4, h5, header, html, img, input, label, li, main, nav, p, span, ul {
    box-sizing: border-box !important;
    font-family: LatoRegular, serif;
    margin: 0;
    padding: 0
    }
address, article, figure, footer, header, nav, section {
    display: block
    }
ul {
    list-style: none
    }
button {
    cursor: pointer
    }
button:hover, input:hover {
    background-color: #202020;
    border-color: #555;
    color: #ddd
    }
button:active, input:focus {
    background-color: #1e1e1e;
    border-color: #666;
    color: #eee
    }
a, a h5, a h5:hover, a:after, a:hover, a:hover:after, button, button:hover, input, input:hover {
    transition: background 0.15s ease-in-out, color 0.15s ease-in-out, border 0.15s ease-in-out
    }
footer {
    background-color: #222;
    font-size: 1.05rem;
    padding-bottom: 1rem;
    padding-top: 1rem
    }
.footer-container {
    display: grid;
    grid-template-areas: "footer-text footer-navigation footer-information footer-contact" "footer-text footer-navigation footer-socials footer-contact";
    grid-template-columns: repeat(4, 1fr);
    margin: 0 auto
    }
.footer-container section {
    padding: 0 1rem
    }
#dark-layer {
    transition: opacity 0.2s
    }
.sidenav {
    transition: width 0.5s
    }
.card-title:hover {
    color: #a56acd
    }
.card-add {
    cursor: pointer
    }
.card-add:hover {
    background-color: #9144bb
    }
.card-add:active {
    bottom: -1px
    }
.newsletter {
    background-color: #333;
    margin-top: 1rem;
    padding: 2rem 0 3rem;
    text-align: center
    }
.newsletter > .container {
    text-align: center;
    width: clamp(50%, 60%, 100%)
    }
#newsletter-email, #newsletter-email + button {
    border-radius: 0.125rem;
    font-size: clamp(1rem, 2vw, 1.1rem);
    margin-top: 1rem;
    padding: clamp(0.875rem, 1.5vw, 1rem);
    width: 60%
    }
.footer-container a {
    color: #a56acd;
    margin-bottom: 0.2rem
    }
.footer-container h5 {
    font-size: 1.1rem;
    font-weight: 700;
    margin: 1rem 0 0
    }
.footer-content {
    margin-top: 0.5rem
    }
.footer-socials a {
    background-color: #444;
    border-radius: 50%;
    color: #ddd;
    display: inline-block;
    height: 2.5rem;
    position: relative;
    text-align: center;
    text-decoration: none;
    width: 2.5rem
    }
.footer-socials a:before {
    font-size: 1.5rem;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%)
    }
.footer-contact address * {
    display: block;
    margin-top: 0.2rem
    }
.footer-contact a:hover, .footer-navigation a:hover {
    color: #eee
    }
.footer-socials a:hover {
    color: #a56acd
    }
.card-add.disabled {
    cursor: auto
    }
.card-add.disabled:active {
    bottom: 0
    }
.dark-layer-fade {
    display: block !important;
    opacity: 0.5 !important;
    transition: opacity 0.2s !important
    }
.blink {
    animation: blink-animation 1s linear 2
    }